h2 {
  font-size: 3.5rem;
}

.portfolio-image-col {
  height: 130vh;
  width: 70%;
  /* background-color: red; */
  margin: 10px;
}

.portfolio-image {
  width: 100%;
  height: 130vh;
} 

.title-row {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .portfolio-image-col {
    width: 100%;
    height: 100vh;
  }

  .portfolio-image {
    height: 100vh;
  }
} 