* {
  margin: 0;
  padding: 0;
  font-family: var(--font-family);
}

:root { 
  --font-family: 'Cormorant Garamond', serif;
  --beige: #F6EFEB;
}

a {
  color: black;
  text-decoration: none;
}

img {
  object-fit: cover;
  object-position: center;
}

p {
  font-size: 1.3rem;
  font-weight: 500;
}

button {
  background-color: transparent;
  border: none;
}